
.toc-page {
    width: 100%;
}

.toc {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 5rem auto
}

.toc-menu {
    display: flex;
    width: 95%;
    margin: 0 auto 1rem auto;
    flex-direction: row;
    justify-content: space-between;
    height: fit-content;
}

.toc a {
    color: inherit;
    text-decoration: none;
}

.toc-card a {
    text-decoration: none;
    color: inherit
}

.toc-card-header {
    flex-direction: row;
    width: 50%;
    height: 4.5rem;
    padding-top: 2rem;
    font-family: 'FrederickatheGreat-Regular', 'Arial Narrow', Arial, sans-serif;
    font-size: large;
    font-weight: normal;
    text-align: center;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    /* margin: 1em auto 1em auto; */
    /* mix-blend-mode: multiply; */
    
 }

 .toc-active {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-style: inset;
    border-color: rgb(148, 147, 147);
    /* border-bottom-style: groove; */
    font-weight: 700;
 }

.toc-card-header:hover {
    cursor: pointer;
    
 }

.card {
    height: 5rem;
    max-width: 90%;
    margin: auto;
    display: flex;
    /* background: whitesmoke; */
    flex-direction: row;
    justify-content: space-between;
    padding: 0.3rem 0.7rem;
    text-align: center;
    /* Add shadows to create the "card" effect */
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    align-items: center;
}

.card p {
    font-size: 1em;
}

.card small {
    color: darkred;
    mix-blend-mode: normal;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
    color: rgb(196, 190, 167);
    /* box-shadow: 0 8px 16px 0 rgba(0,0,0,0.3); */
  }
  

  /* Add some padding inside the card container */
.container {
    display: flex;
    flex-direction: row;
    padding: 0.1rem;
    align-items: center;
  }
