ul {
    list-style: none;
}

li {
  margin-bottom: 1rem;
}

.coverpage {
    max-height: 100%;
    margin: 0 2rem 0 2rem;
    align-content: center;
}

#cross-logo {
    display: block;
    max-width: 240px;
    height: auto;
    margin: auto;
}

.booktitle {
  text-align: center;
  margin-top: 0px
}

.booktitle a {
    color: inherit;
    text-decoration: none;
}

.booktitle b {
    font-size: x-large;
}

.booktitle small {
    font-size: x-small;
}

.randomverse {
    width: 260px;
    margin: auto;
}

.randomverse p {
    font-size: 1.45rem;
    text-align: center;
}

.randomverse a {
    text-decoration: none;
    color: inherit;
}

.randomverse .bibleposition {
    color: darkred;
    padding: 0 1rem 0 1rem;
}

.settings {
    width: 70%;
    margin: 30% auto;
    font-size: 2rem;
}

.settings-item {
    display: flex;
    justify-content: space-between;
}

.settings-item label {
    display: inline-block;
    margin: 20px 0px;
}

.settings-item select {
    display: inline-block;
    height: fit-content;
    margin: 15px;
    border-radius: 0.25em;
    font-size: 2rem;
}

.sharelinks {
    display: flex;
    max-width: 6rem;
    margin: auto;
    align-content: center;
    justify-content: space-around;
    margin-top: 1rem;
}


.verses-list {
    width: 90%;
 }

.verse-detail {
     margin-top: 0.5rem;
     line-height: normal;
     align-items: center;
 }

.verse-detail p {
     text-align: left;
     margin-bottom: 0;
 }

.verse-detail small {
     color: darkred;
     margin-left: 2rem;
 }

 .verse-detail em {
     margin-left: 0.5rem;
 }

 .verse-detail:hover {
    /* color: #efefef; */
    /* background: #7d7d7d; */
    /* mix-blend-mode: darken; */
    /* text-decoration: none; */
    cursor: pointer;
 }

 .verse-detail a {
    color: inherit;
    text-decoration: none;
 }

.chapter-number {
      font-size: 4rem;
      color: darkred;
      mix-blend-mode: normal;
}

.chapter {
     justify-content: center;
     text-align: left;
     margin: 1rem 5% 7rem 5%;
     line-height: 2em;
 }

.chapter sup {
     color: darkred;
     mix-blend-mode: normal;
 }

 .close-page {
     position: absolute;
     top: 5px;
     right: 5px;
 }

.about-page {
     margin: 7rem 1rem;
 }

.about-text {
     margin-top: 2rem;
 }

.about-footer {
     font-style: italic;
     font-size: smaller;
     position: fixed;
     bottom: 2rem;
 }
