.page-content {
  --btn-font-size: 1.4rem;
  --btn-font-color: rgb(122, 120, 120);
}

.bookmark-list-title {
  font-family: "FrederickatheGreat-Regular", sans-serif;
  width: fit-content;
  margin: auto;
}

.bookmark-list {
  width: 95%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.btn-note {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.2rem;
  margin: 0rem 0 0rem 0;
  font-size: var(--btn-font-size);
  color:var(--btn-font-color);
  background: lightgrey;
  cursor: pointer;
}

.btn-note:hover {
  background: gray;
  color: aliceblue;
}

.note-details {
  width: 80%;
  margin: auto;
  align-items: center;
  display: flex;
  flex-direction: column; 
  justify-content: center  
}

.note-details small {
  font-size: 1.3rem;
  margin-bottom: 2rem;  
}

.note-details span {
  font-family: 'FrederickatheGreat-Regular', sans-serif;
  margin-bottom: 1rem;
}

.note-details a {
  text-decoration: none;
}

.note-details textarea {
  min-height: 20vh;
  font-family: 'Merriweather-Regular', sans-serif;
  margin-bottom: 1rem;
}