
#menu-overlay {
  position: fixed;
  top: 5px;
  left: 5px;
  z-index: 50;
  height: auto;
  border-radius: 25px;
  overflow: hidden;
  background: rgba(68, 68, 68, 0.99);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  transition: all 0.5s ease;
}

#menu-overlay > * {
  float: left;
}

#menu-overlay-toggle {
  display: block;
  cursor: pointer;
  opacity: 0;
  z-index: 999;
  margin: 0;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 0;
}

#menu-overlay-toggle:checked ~ ul {
  width: 180px;
  height: 450px;
  font-size: medium;
  background-position: 0px -50px;
}

#menu-overlay ul {
  list-style-type: none;
  margin: 0;
  padding: 0 0 0 50px;
  height: 50px;
  width: 0px;
  transition: 0.5s width ease;
  background-image: url("../assets/toggleimg.png");
  background-repeat: no-repeat;
  background-position: 0px 0px;
}

#menu-overlay li:first-child {
  margin-top: 60px;
}

#menu-overlay li {
  display: inline-block;
  cursor: pointer;
  line-height: 50px;
  width: 180px;
  text-align: left;
  margin: 0.5rem 0 0.5rem -40px;
}

#menu-overlay li a {
  font-size: 1.25em;
  font-weight: bold;
  color: white;
  text-decoration: none;
}
