.bookmark-card {
    width: 90%;
    margin: 0.5rem;
    border-style: solid;
    border-color: darkgray;
    border-width: 0.1rem;
    border-bottom-right-radius: 1rem;
    border-top-left-radius: 1rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    /* box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); */
    transition: 0.3s;
  }
  
  .bookmark-card a {
    color: inherit;
  }
  
  .book-chapter-tag {
    width: 20%;
    margin: auto;
    padding-left: 0.7rem;
    display: flex;
    font-size: 1rem;
    flex-direction: row;
    text-align: center;
    text-decoration: none;
  }

  .bookmark-remark {
    width: 50%;
    margin: auto;
    display: flex;
    font-size: 1.2rem;
    flex-direction: row;
    text-align: center;
    text-decoration: none;
  }
  
  .icons-toolbar {
    display: inline-flex;
    flex-direction: row;
    width: 30%;
    justify-content: space-between;
  }